import { connectAsync, MqttClient as Client, IPublishPacket } from 'mqtt';
import { ScryptedDeviceBase } from '@scrypted/sdk';
import { sleep } from "../../scrypted/server/src/sleep";

export type MqttMessageCb = (topic: string, message: string, data: IPublishPacket) => Promise<void>

const topicMatches = (pattern: string, topic: string): boolean => {
    const patternLevels = pattern.split('/');
    const topicLevels = topic.split('/');

    for (let i = 0; i < patternLevels.length; i++) {
        if (patternLevels[i] === '#') return true;
        if (patternLevels[i] === '+') continue;
        if (patternLevels[i] !== topicLevels[i]) return false;
    }

    return patternLevels.length === topicLevels.length;
}

export default class MqttClient {
    mqttClient: Client;
    mqttPathmame: string;
    host: string;
    username: string;
    password: string;
    clientId?: string;
    topicLastValue: Record<string, any> = {};
    logger: Console;
    topicCbMap: Record<string, MqttMessageCb[]> = {};
    connecting: boolean;
    configTopicPattern: string;
    currentAutodiscoveryTopics: string[] = [];

    constructor(
        host: string,
        username: string,
        password: string,
        logger: Console,
        clientId?: string,
        configTopicPattern?: string,
    ) {
        this.host = host;
        this.username = username;
        this.password = password;
        this.logger = logger;
        this.clientId = clientId;
        this.configTopicPattern = configTopicPattern;
    }

    async disconnect() {
        if (this.mqttClient) {
            try {
                this.logger.log('Disconnecting mqtt client');
                await this.mqttClient.endAsync(true);
            } catch (e) {
                this.logger.log('Error closing MQTT connection', e);
            }
        }
    }

    async getMqttClient(forceReconnect?: boolean): Promise<Client> {
        if (this.connecting) {
            return this.mqttClient;
        }

        const _connect = async () => {
            try {
                this.connecting = true;
                const client = await connectAsync(this.mqttPathmame, {
                    rejectUnauthorized: false,
                    username: this.username,
                    password: this.password,
                    clientId: this.clientId,
                    reschedulePings: true,
                }, true);

                if (this.configTopicPattern) {
                    await client.subscribeAsync([this.configTopicPattern]);
                }

                client.on('message', async (messageTopic, message, data) => {
                    const cbs: MqttMessageCb[] = [];
                    for (const [key, data] of Object.entries(this.topicCbMap)) {
                        if (topicMatches(key, messageTopic)) {
                            cbs.push(...data)
                        }
                    }
                    const messageString = message.toString();

                    if (cbs?.length) {
                        this.logger.info(`Topic ${messageTopic} sent with data ${message}: ${JSON.stringify(data)}`);
                        for (const cb of cbs) {
                            await cb(messageTopic, messageString, data);
                        }
                    }

                    if (this.configTopicPattern) {
                        !!message &&
                            messageTopic.endsWith('/config') &&
                            !this.currentAutodiscoveryTopics.includes(messageTopic) &&
                            this.currentAutodiscoveryTopics.push(messageTopic);
                    }
                });

                client.setMaxListeners(Infinity);

                this.logger.log('Connected to MQTT', this.mqttPathmame, this.username, this.clientId);
                this.mqttClient = client;
            } catch (e) {
                this.logger.log('Error connecting to mqtt. Waiting before trying again. Client id is ', this.clientId, e);
                await this.mqttClient?.endAsync(true);
                await sleep(5000);
            } finally {
                this.connecting = false;
            }
        }

        if (!this.mqttClient || forceReconnect) {
            if (forceReconnect && this.mqttClient) {
                try {
                    await this.mqttClient.endAsync();
                } catch (e) { }
            }
            const url = this.host;
            const urlWithoutPath = new URL(url);
            urlWithoutPath.pathname = '';

            this.mqttPathmame = urlWithoutPath.toString();
            if (!this.mqttPathmame.endsWith('/')) {
                this.mqttPathmame = `${this.mqttPathmame}/`;
            }
            this.logger.log('Starting MQTT connection', this.mqttPathmame, this.username, this.clientId);

            await _connect();

            // if (this.mqttClient) {
            //     this.mqttClient.onKeepaliveTimeout = async () => {
            //         this.logger.log('keepalive timeout. Reconnecting');
            //         this.mqttClient.reconnect();
            //     }
            // }
            return this.mqttClient;
            // } else if (!this.mqttClient.connected) {
            //     this.logger.log('MQTT disconnected. Reconnecting', this.host, this.username, this.mqttPathmame);

            //     await _connect();
            //     return this.mqttClient;
        } else {
            return this.mqttClient;
        }
    }

    async publish(topic: string, inputValue: any, retain = true) {
        let value: string | Buffer<ArrayBufferLike>;
        try {
            if (typeof inputValue === 'object')
                value = JSON.stringify(inputValue);
            if (inputValue.constructor.name !== Buffer.name)
                value = inputValue.toString();
        } catch (e) {
            this.logger.log(`Error parsing publish values: ${JSON.stringify({ topic, value })}`, e);
            return;
        }

        if (this.topicLastValue[topic] === value) {
            this.logger.debug(`Skipping publish, same as previous value: ${JSON.stringify({ topic, value, previousValue: this.topicLastValue[topic] })}`);

            return;
        }

        this.logger.debug(`Publishing ${JSON.stringify({ topic, value })}`);
        const client = await this.getMqttClient();
        if (client) {
            try {
                await client.publishAsync(topic, value, { retain });
            } catch (e) {
                this.logger.log(`Error publishing to MQTT. Reconnecting. ${JSON.stringify({ topic, value })}`, e);
                await this.getMqttClient(true);
                await client.publishAsync(topic, value, { retain }).catch(e => this.logger.log(`Error publish retry. ${JSON.stringify({ topic, value })}`, e));
            } finally {
                this.topicLastValue[topic] = value;
            }
        }
    }

    public getMqttTopicTopics<Domain extends string = string>(pluginName: string, device: ScryptedDeviceBase) {
        const deviceId = device.id;

        const getEntityTopic = (entity: string) => `scrypted/${pluginName}/${deviceId}/${entity}`;
        const getInfoTopic = (entity: string) => `${getEntityTopic(entity)}/info`;
        const getDiscoveryTopic = (domain: Domain, entity: string) => `homeassistant/${domain}/${pluginName}-${deviceId}/${entity}/config`;

        return {
            getEntityTopic,
            getDiscoveryTopic,
            getInfoTopic,
        }
    }

    async subscribe(topics: string[], cb: MqttMessageCb) {
        await this.unsubscribe(topics);
        const client = await this.getMqttClient();

        if (client) {
            await client.subscribeAsync(topics);

            for (const topic of topics) {
                if (!this.topicCbMap[topic]) {
                    this.topicCbMap[topic] = [];
                }

                this.topicCbMap[topic].push(cb);
            }
        }
    }

    async unsubscribe(topics: string[]) {
        const client = await this.getMqttClient();
        if (client) {
            await client.unsubscribeAsync(topics);

            for (const topic of topics) {
                delete this.topicCbMap[topic];
            }
        }
    }

    async removeAllListeners() {
        const mqttClient = await this.getMqttClient();

        if (mqttClient) {
            mqttClient.removeAllListeners();
        }
    }

    async cleanupAutodiscoveryTopics(activeTopics: string[]) {
        const topicsToDelete = this.currentAutodiscoveryTopics.filter(topic => !activeTopics.includes(topic));
        if (topicsToDelete.length > 0) {
            for (const topic of topicsToDelete) {
                await this.publish(topic, '', true);
            }
            this.logger.log(`${topicsToDelete.length} topics to delete found: ${topicsToDelete.join(', ')}`);
        }
    }
}
